import React from "react";
import "./style.scss";
import { useEffect } from "react";
import qrcode from "qrcode";
import classNames from "classnames";
import executeAsync from "fetchSample";
import { useState } from "react";

const MobileCRM = window.MobileCRM;

const InspectorPopup = ({ visible, closePopup }) => {
  const [qr, setQr] = useState(null);
  const [organization, setOrganization] = useState(null);
  const [mail, setMail] = useState(null);
  useEffect(() => {
    if (!qr) {
      MobileCRM.Configuration.requestObject(
        (config) => {
          let { settings } = config;
          const organizationName = settings.crmOrganization;
          const { systemUserId } = settings;
          executeAsync(systemUserId, (mail) => {
            let output = `url=https://inspections.resco.net/${organizationName}\nusermode=0\nusername=${mail}\nsavepassword=1`;
            const base64 = qrcode.toDataURL(output, 4);
            setQr(base64);
            setMail(mail);
            setOrganization(organizationName);
          });
        },
        (err) => {
          console.log("An error occurred: " + err);
        },
        null
      );
    }
    if (visible) {
      document.body.style.overflow = "hidden";
    } else {
      setTimeout(() => {
        document.body.style.overflow = "initial";
      }, 400);
    }
  }, [visible]);

  const classList = classNames("inspector-popup__wrapper", {
    "inspector-popup__wrapper--visible": visible,
  });
  return (
    <div className={classList}>
      <section className="inspector-popup">
        <div
          className="inspector-popup__qr"
          style={{ backgroundImage: `url(${qr})` }}
        ></div>
        <section className="inspector-popup__section">
          <h1 className="inspector-popup__header inspector-popup--green">
            Download the Resco Inspections mobile app
          </h1>
          <ul className="inspector-popup__list">
            <li className="inspector-popup__list-item">
              1. Download the mobile app for{" "}
              <a href="https://itunes.apple.com/ua/app/resco-inspections/id1357591626?mt=8">
                iOS
              </a>
              ,{" "}
              <a href="https://play.google.com/store/apps/details?id=net.resco.inspections">
                Android
              </a>{" "}
              or{" "}
              <a href="https://www.microsoft.com/en-us/p/resco-inspections/9n4gsvt8bnfj">
                Windows
              </a>
            </li>
            <li className="inspector-popup__list-item">2. Run the app</li>
            <li className="inspector-popup__list-item">
              3. On the ‘Welcome to Resco Mobile CRM’ screen select ‘Internal
              User’
            </li>
            <li className="inspector-popup__list-item">
              4. Enter the URL{" "}
              <span className="inspector-popup--green">
                https://inspections.resco.net/{organization || "organization"}
              </span>
              , username{" "}
              <span className="inspector-popup--green">{mail || ""}</span>, and
              password
            </li>
          </ul>
          <p className="inspector-popup__paragraph">
            …or simply hit the{" "}
            <span className="inspector-popup--green">‘Scan QR’</span> button and
            scan the QR code.
          </p>
        </section>
        <i className="inspector-popup__close" onClick={closePopup}></i>
      </section>
    </div>
  );
};

export default InspectorPopup;
