import React from 'react';
import './style.scss';

const CardsContainer = ({ header, description, children }) => {
    return (
        <section className="cards-container">
            <h1 className="cards-container__header">{header}</h1>
            {description
            ? <p className="cards-container__description">{description}</p>
            : null
            }
            <div className="cards-container__wrapper">
                {children}
            </div>
        </section>
    )
}

export default CardsContainer;