import React, { useEffect, useState } from "react";

import { Card, CardsContainer, InspectorPopup } from "components";
import executeAsync from "fetchSample";

const MobileCRM = window.MobileCRM;

function App() {
  const [inspectorPopup, setInspectorPopup] = useState(false);
  const [homeFormItems, setHomeFormItems] = useState(null);

  useEffect(() => {
    if (!homeFormItems) {
      MobileCRM.UI.HomeForm.requestObject((homeForm) => {
        if (homeForm)
          setHomeFormItems(
            homeForm.items.map((item) => ({
              text: item.title,
              name: item.name,
            }))
          );
      }, console.log);
    }
  }, []);
  const toggleInspectorPopup = () => {
    inspectorPopup ? setInspectorPopup(false) : setInspectorPopup(true);
  };

  const handleHomeItem = (selector) => {
    if (!homeFormItems) {
      console.log("homeFormItems not found");
      return;
    }

    const homeFormItem = homeFormItems.find((item) => item.text === selector);
    if (!homeFormItem) {
      console.log("homeFormItem not found in: ", homeFormItems);
      return;
    }

    MobileCRM.UI.HomeForm.openHomeItemAsync(
      homeFormItem.name,
      console.log,
      null
    );
  };

  return (
    <>
      <CardsContainer header="ALL MODULES">
        <Card
          onClick={handleHomeItem}
          homeItem="Questionnaire Designer"
          iconSrc="icons/templates_designer.png"
          header="Questionnaire Designer"
          description="Build your questionnaires from scratch."
        />
        <Card
          onClick={handleHomeItem}
          homeItem="Schedule Board"
          iconSrc="icons/schedule_board.png"
          header="Schedule Board"
          description="Assign appointments to users, plan work for your field team, and see their progress."
        />

        {/* Exception, doesn't open homeItem */}
        <Card
          onClick={toggleInspectorPopup}
          iconSrc="icons/inspector.png"
          header="Inspector"
          description="App for executing inspections and filling out questionnaires in the field."
        />

        <Card
          onClick={handleHomeItem}
          homeItem="Dashboard"
          iconSrc="icons/automatic_dashboard.png"
          header="Dashboard"
          description="Evaluate and visualize the data sent back by the inspectors."
        />
        <Card
          onClick={handleHomeItem}
          homeItem="Result Viewer"
          iconSrc="icons/result_viewer.png"
          header="Result Viewer"
          description="Evaluate and visualize the data sent back by the inspectors."
        />
        <Card
          onClick={handleHomeItem}
          homeItem="Submitted Reports"
          iconSrc="icons/report_designer.png"
          header="Submitted Reports"
          description="Enables you to set up mobile reports that provide a clear-cut overview of your data."
        />
        <Card
          onClick={handleHomeItem}
          homeItem="Woodford: Manage Apps"
          iconSrc="icons/woodford.png"
          header="App Configurator"
          description="Customize the mobile app to fit your requirements."
        />
        <Card
          onClick={handleHomeItem}
          homeItem="Admin Console: Manage Organization"
          iconSrc="icons/admin_console.png"
          header="Manage Organization"
          description="Resco Cloud settings and customization."
        />
      </CardsContainer>
      <CardsContainer
        header="CONNECTORS"
        description="Use connectors for Dynamics 365, Salesforce, or SAP, to take the full advantage of Inspections integrated with your own backend."
      >
        <Card
          href="mailto:sales@resco.net?subject=Connector for Resco Inspections: Dynamics CRM"
          iconSrc="icons/dynamics.png"
          header="Dynamics 365/CRM"
          description="Use Inspections directly in your Dynamics 365 environment "
        />
        <Card
          href="mailto:sales@resco.net?subject=Connector for Resco Inspections: Salesforce"
          iconSrc="icons/salesforce.png"
          header="Salesforce"
          description="Connect and work with your Salesforce data instantly and securely."
        />
        <Card
          href="mailto:sales@resco.net?subject=Connector for Resco Inspections: SAP"
          iconSrc="icons/sap.png"
          header="SAP"
          description="Connect to your ERP and work with your data."
        />
      </CardsContainer>
      <InspectorPopup
        visible={inspectorPopup}
        closePopup={toggleInspectorPopup}
      />
    </>
  );
}

export default App;

