import React from 'react';
import './style.scss';

const Card = ({ iconSrc, header, description, onClick, homeItem, href }) => {
    return (
        <div className="card" onClick={onClick ? () => onClick(homeItem) : href ? () => {
            window.open(href);
        } : () => {}}> 
            <i className="card__icon" style={{backgroundImage: `url('${iconSrc}')`}}></i>
            <h1 className="card__header">{header}</h1>
            <p className="card__description">{description}</p>
        </div>
    )
}

export default Card;